.category{
    margin-top: 70px;
    background-color: rgb(255, 255, 255);
}
@media screen and (max-width: 768px) {
  .category{
    margin-top: 110px;
  }
}


.container{
  margin-top: 60px;
}
@media screen and (max-width: 768px) {
  .container{
    margin-top: 110px;
  }
}

.category button {
    text-decoration: none;
    color: white;
    background-color: #f20c0c; /* replace with your desired color */
    border: none;
    padding: 5px 30px;
    margin-right: 10px;
    border-radius: 5px;
    cursor: pointer;
    margin: 4px;
    margin-bottom: 10px; /* add margin between the buttons */
  }
  
  .category button:last-child {
    margin-right: 0; /* remove the margin-right from the last button */
  }
  
  .category button:hover {
    background-color: #0059ff; /* replace with your desired color */
    color: white;
  }

  p.text-white.text-center {
    margin: 8px 60px;
}