 #t1{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
   #t2{
    width: 250px;
    height: 400px;
    box-shadow: 2px 2px 10px rgb(45 40 35);
    border-radius: 10px;
    overflow: hidden;
    margin: 20px;
  }
  
  .card img {
    width: 100%;
    height: 60%;
    /* object-fit: cover; */
  }
  
  .card-body {
    padding: 1rem;
  }
  
  .card-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .card .price {
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 1rem;
  }

  

  